import React from "react";
// import { Header, Icon, Image, Menu, Segment, Sidebar } from "semantic-ui-react";
import "../App.css";
import { scroller, Link } from "react-scroll";
import { NavLink } from "react-router-dom";
class Side extends React.Component {
  render() {
    const scrollToElement = element => {
      scroller.scrollTo(element, {
        duration: 200,
        delay: 100,
        smooth: true
      });
    };

    return (
      <div>
        <div className="sidenav">
          <p onClick={() => scrollToElement("Home")}>Home</p>

          <p onClick={() => scrollToElement("About")}>About</p>
          <p onClick={() => scrollToElement("Education")}>Education</p>
          <p onClick={() => scrollToElement("Projects")}>Projects</p>
          <p onClick={() => scrollToElement("Skills")}>Skills</p>
          <p onClick={() => scrollToElement("Footer")}>Contact</p>
        </div>
      </div>
    );
  }
}

export default Side;
